.form {
  display: flex;
  align-items: flex-end;
}

.form button {
  display: inline-block;
  font: inherit;
  font-size: 14px;
  cursor: pointer;
  background-color: #ee4c14;
  border: none;
  outline: none;
  color: white;
  padding: 6px 14px;
  height: fit-content;
  border-radius: 4px;
}

@media (hover: hover) {
  .form button:hover {
    background-color: #a6350e;
  }
}

@media (hover: none) {
  .form button:active {
    background-color: #a6350e;
  }
}
